:root {
  --header-bg-color: #fff;
  --text-color-main: #000;
  --text-color-second: #00acdc;
  --page-bg-color: #e5e5e5;
  --header-height: 60px;
}

html {
  background-color: var(--page-bg-color);
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  height: var(--header-height);
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo, .header__logo-link {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list-item {
  height: var(--header-height);
  margin-left: 20px;
  list-style-type: none;
  display: flex;
  position: relative;
}

.nav__link {
  text-transform: uppercase;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.nav__link:link, .nav__link:visited {
  color: var(--text-color-main);
}

.nav__link:hover, .nav__link.is-active {
  color: var(--text-color-second);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--text-color-second);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__list-item:first-child {
  margin-left: 0;
}

